"use client";

import React, { useState, useEffect } from "react";
import axios from "axios";
import AnimatedNumber from "../.././components/AnimateNumber";

// Hero UI
import {
    Button,
    Card,
    CardBody,
    Table,
    TableHeader,
    TableColumn,
    TableBody,
    TableRow,
    TableCell,
} from "@heroui/react";

// Heroicons
import {
    ShieldCheckIcon,
    UserGroupIcon,
    BanknotesIcon,
    ClockIcon,
} from "@heroicons/react/24/outline";

import { motion } from "framer-motion";

const HospitalCash = () => {
    const [currency, setCurrency] = useState("KES");
    const [exchangeRates, setExchangeRates] = useState<Record<string, number>>({});
    const [loadingRates, setLoadingRates] = useState(false);

    const currencySymbols: Record<string, string> = {
        KES: "Ksh.",
        USD: "$",
        EUR: "€",
        GBP: "£",
    };

    useEffect(() => {
        const fetchExchangeRates = async () => {
            try {
                setLoadingRates(true);
                const response = await axios.get(
                    "https://snownet-customer-quotation-server.onrender.com/api/underwriting/quotation/onlineQuotation/products/exchangeRate/"
                );
                const ratesArray = response.data?.data || [];
                const ratesMap: Record<string, number> = {};
                for (let item of ratesArray) {
                    if (item.currency && item.x_rate) {
                        ratesMap[item.currency] = parseFloat(item.x_rate);
                    }
                }
                setExchangeRates(ratesMap);
            } catch (error) {
                console.error("❌ Failed to fetch exchange rates:", error);
            } finally {
                setLoadingRates(false);
            }
        };

        fetchExchangeRates();
    }, []);

    const getConvertedValue = (amount: number) => {
        const rate = exchangeRates[currency] || 1;
        return Number(amount) * rate;
    };

    const handleClickHospital = () => {
        window.location.href = "https://quote.birdviewinsurance.com/?ProductID=4";
    };

    return (
        <div className="relative bg-gray-50 py-12">
            <div className="container mx-auto px-4">
                {/* Intro Section */}
                <motion.div
                    initial={{ opacity: 0, y: 40 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.6 }}
                    viewport={{ once: true }}
                    className="text-center max-w-3xl mx-auto mb-12"
                >
                    <h2 className="text-3xl md:text-4xl font-bold text-gray-900">
                        Hospital Cash Cover
                    </h2>
                    <p className="text-gray-600 mt-4">
                        Our hospital cash product provides daily payments for insureds
                        admitted in hospital for up to a maximum of 10 payments per year.
                        Payments start from the second day of admission, ranging from
                        <span className="font-semibold"> Ksh. 2,000 </span> to{" "}
                        <span className="font-semibold"> Ksh. 5,000 </span> daily.
                    </p>
                </motion.div>

                {/* Pricing Table */}
                <motion.div
                    initial={{ opacity: 0, y: 50 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.6, delay: 0.2 }}
                    viewport={{ once: true }}
                    className="bg-white rounded-2xl shadow-lg p-6 mb-12"
                >
                    <Table aria-label="Hospital Cash Plans">
                        <TableHeader>
                            <TableColumn align="center">Plan</TableColumn>
                            <TableColumn align="center">Daily Cash Payment</TableColumn>
                            <TableColumn align="center">Annual Premium</TableColumn>
                        </TableHeader>
                        <TableBody>
                            {[
                                { plan: "Plan 1", cashPayment: 2000, premium: 1116 },
                                { plan: "Plan 2", cashPayment: 2500, premium: 1392 },
                                { plan: "Plan 3", cashPayment: 3000, premium: 1668 },
                                { plan: "Plan 4", cashPayment: 3500, premium: 1944 },
                                { plan: "Plan 5", cashPayment: 4000, premium: 2220 },
                                { plan: "Plan 6", cashPayment: 5000, premium: 2772 },
                            ].map((row, index) => (
                                <TableRow key={index}>
                                    <TableCell align="center" className="font-medium">
                                        {row.plan}
                                    </TableCell>
                                    <TableCell
                                        align="center"
                                        className="text-primary font-semibold"
                                    >
                                        {currencySymbols[currency]}{" "}
                                        <AnimatedNumber value={getConvertedValue(row.cashPayment)} />
                                    </TableCell>
                                    <TableCell
                                        align="center"
                                        className="text-green-600 font-semibold"
                                    >
                                        {currencySymbols[currency]}{" "}
                                        <AnimatedNumber value={getConvertedValue(row.premium)} />
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </motion.div>

                {/* Stats Section */}
                <motion.div
                    initial={{ opacity: 0 }}
                    whileInView={{ opacity: 1 }}
                    transition={{ duration: 0.8 }}
                    viewport={{ once: true }}
                    className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-6 mb-12"
                >
                    <Card className="bg-white shadow-md rounded-xl">
                        <CardBody className="flex flex-col items-center text-center p-6">
                            <ShieldCheckIcon className="h-10 w-10 text-primary mb-3" />
                            <h4 className="text-lg font-bold text-gray-900">
                                <AnimatedNumber value={5000} prefix="Ksh. " />
                            </h4>
                            <p className="text-gray-600 text-sm">Daily Benefit Payout</p>
                        </CardBody>
                    </Card>

                    <Card className="bg-white shadow-md rounded-xl">
                        <CardBody className="flex flex-col items-center text-center p-6">
                            <UserGroupIcon className="h-10 w-10 text-primary mb-3" />
                            <h4 className="text-lg font-bold text-gray-900">
                                <AnimatedNumber value={50000} />+
                            </h4>
                            <p className="text-gray-600 text-sm">Families Protected</p>
                        </CardBody>
                    </Card>

                    <Card className="bg-white shadow-md rounded-xl">
                        <CardBody className="flex flex-col items-center text-center p-6">
                            <ClockIcon className="h-10 w-10 text-primary mb-3" />
                            <h4 className="text-lg font-bold text-gray-900">
                                <AnimatedNumber value={48} /> Hours
                            </h4>
                            <p className="text-gray-600 text-sm">Claims Settlement</p>
                        </CardBody>
                    </Card>

                    <Card className="bg-white shadow-md rounded-xl">
                        <CardBody className="flex flex-col items-center text-center p-6">
                            <BanknotesIcon className="h-10 w-10 text-primary mb-3" />
                            <h4 className="text-lg font-bold text-gray-900">Nationwide</h4>
                            <p className="text-gray-600 text-sm">Coverage Available</p>
                        </CardBody>
                    </Card>
                </motion.div>

                {/* CTA */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.6 }}
                    viewport={{ once: true }}
                    className="text-center"
                >
                    <p className="text-gray-700 text-lg font-semibold mb-4">
                        Ready to take the next step?
                    </p>
                    <Button
                        size="lg"
                        className="bg-primary text-white font-semibold px-6"
                        onClick={handleClickHospital}
                        startContent={<ShieldCheckIcon className="h-5 w-5" />}
                    >
                        Get Quote
                    </Button>
                </motion.div>
            </div>
        </div>
    );
};

export default HospitalCash;
